import Link from 'next/link';

const Navbar = () => (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark mb-5">
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
            <a className="navbar-brand" href="#">
                <img src="https://www.pinclipart.com/picdir/middle/0-7807_clip-art-facebook-logo-facebook-clipart-pictures-46269.png" />
            </a>
            <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                <li className="nav-item active">
                    <a className="nav-link" href="#">Home <span className="sr-only">(current)</span></a>
                </li>
                <li className="nav-item">
                    <a className="nav-link" href="#">Catalog</a>
                </li>
                <li className="nav-item dropdown">
                    <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Collections</a>
                    <div className="dropdown-menu shadow-sm">
                        <a className="dropdown-item" href="#">Carabiners</a>
                        <a className="dropdown-item" href="#">Ropes</a>
                        <a className="dropdown-item" href="#">Harnesses</a>
                    </div>
                </li>
                <li className="nav-item dropdown">
                    <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">Categories</a>
                    <div className="dropdown-menu shadow-sm">
                        <a className="dropdown-item" href="#">Sport</a>
                        <a className="dropdown-item" href="#">Trad</a>
                        <a className="dropdown-item" href="#">Aid</a>
                    </div>
                </li>
            </ul>
            <a className="nav-link text-white cart right">Cart</a>
        </div>
        <style jsx>{`
            .nav-item {
                display: flex;
                align-items: center;
                height: 4rem;
            }
            .dropdown-menu {
                border-top-left-radius: 0;
                border-top-right-radius: 0;
            }
        `}</style>
    </nav>
)

export default Navbar